import "./index.scss";
const img = require("../../../assets/shoujianxiang.jpg");
import Msgtoggle from "../../mobx";
import {observer} from "mobx-react";
import {axios} from "&/axios";
import {history} from "&/history";

@observer

export default class Shoujianxiang extends Component{
    constructor(){
        super();
        this.state = {
            endorder:[],
            show:false
        }
    }
    goChat=id=>{
        sessionStorage.houseid = id;
        history.push("/chat");
    }
    componentDidMount(){
        if(sessionStorage.userPhone){
            Msgtoggle.msgRead();
            sessionStorage.checked = 0;
            axios.get("/airbnb/order",{
                params:{
                    phone:sessionStorage.userPhone
                }
            }).then(res=>{
                if(res.data.result){
                    this.setState({
                        endorder: res.data.result.endorder
                    })
                    if(!res.data.result.endorder.length){
                        this.setState({
                            show:true
                        })
                    }
                }else{
                    this.setState({
                        show:true
                    })
                }
            })
        }else{
            this.setState({
                show:true
            })
        }
    }
    render(){
        const {
            endorder,
            show
        } = this.state;
        return (
            <div className="shoujianxiang">
                <div className="em_hd">
                    <h2>收件箱</h2>
                    <p>来自房东/体验达人的消息将出现在这里。</p>
                </div>
                <div className="empty" style={{display:show?"":"none"}}>
                    <div className="em_img">
                        <img src={img} alt=""/>
                    </div>
                </div>
                <div className="fill" style={{display:show?"none":""}}>
                    <ul className="chatlist">
                        {
                            endorder.map((e,i)=>{
                                return(
                                    <li key={i} onClick={()=>this.goChat(e.houseid)}>
                                        <img src={e.pic} alt=""/>
                                        <div className="chattxt">
                                            <h2>您已预订一次前往{e.city}的出行</h2>
                                            <p>Thank you for your reservation.Please...</p>
                                            <h5><span>订单已确认</span>·{e.start}-{e.end}</h5>
                                        </div>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        )
    }
}